<template>
  <div class="info">
    <div class="coin-trade">
        <div class="clearfix table-title">
          <p class="pull-left title1">
            <!-- <i class="el-icon-arrow-down"></i>  -->
            {{ $t('tradeDetail.info.part1') }}</p>
          <!-- <router-link to="/numberAsset" class="pull-right">{{ $t('tradeDetail.info.part2') }}</router-link> -->
        </div>
        <el-row> 
          <el-col :span="12" class="coin-trade-detail">
            <div class="icon clearfix">
              <img :src="info.currency_logo" alt=""  class="pull-left"  id="intro-logo"/>
              <div class="pull-left">
                <div class="title1">{{ info.currency_mark }}</div>
                <p id="intro-name">{{ info.currency_name }}</p>
              </div>
            </div>
            <div class="intro-title">{{ $t('translateLang.tradeDetail.text9') }}</div>
            <p class="coin-des" id="intro-content">
          {{ info.currency_content }}
            </p>
          </el-col>
          <el-col :span="12" class="more-detail">
            <div class="title">{{ $t('tradeDetail.info.part4') }}</div>
            <p>{{ $t('tradeDetail.info.part5') }}<span id="intro-create-time">{{ info.issued_time }}</span></p>
            <p>{{ $t('tradeDetail.info.part6') }}<span id="issued-num">{{ info.issued_num }}</span></p>
            <p>{{ $t('tradeDetail.info.part7') }}<span id="circulate-num">{{ info.circulate_num }}</span></p>
            <p>{{ $t('tradeDetail.info.part8') }}<span id="intro-time">{{ info.block_time }}</span></p>
            <p>{{ $t('tradeDetail.info.part9') }}<span id="intro-reward">{{ info.block_rewards }}</span></p>
            <p>{{ $t('tradeDetail.info.part10') }}<a :href="info.link_url" id="intro-url" v-if="info.link_url!='0'">  {{ info.link_url }}</a></p>
          </el-col>
        </el-row>
      </div>
  </div>
</template>

<script>
// import { getCurrencyIntroduce } from '@/api/trade'
export default {
  props: ['info'],
  data() {
    return {
      // isHidden: false
    }
  }
  // created() {
  //   this.getCurrencyIntroduce()
  // },
  // methods: {
  //   getCurrencyIntroduce() {
  //     const data = {
  //       'currency_id': this.$route.query.currency_id
  //     }
  //     getCurrencyIntroduce(data).then(res => {
  //       if (res.status !== 1000) {
  //         return false
  //       }
  //       this.info = { ...res.data }
  //       var div = document.createElement('div')
  //       div.innerHTML = this.info.currency_content
  //       this.info.currency_content = div.innerText || div.textContent
  //     }).catch(() => {
  //     })
  //   }
  // }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .info {
    font-size: 12px;
    color: #61688a;
    padding: 30px 32px;
    background: #191b2b;
    .title{
      font-size: 14px;
      margin: 10px 0;
    }
    .title1{
      font-size: 14px;
      margin: 10px 0;
      color: #61688a !important;
    }
    .intro-title{
      font-size: 14px;
      line-height: 50px;
    }
    .table-title a:hover {
        color: #1fa8f6;
        text-decoration: underline;
    }
    .table-title a{
      color: #1fa8f6;
      font-size: 12px;
      margin: 10px 0 20px;
      padding-left: 10px;
    }
    .coin-trade{
      margin-top: 20px;
    }
    .icon p{
      color: #707070;
      line-height: 30px;
      
    }
    .icon img{
      margin-right: 10px;
    }
    .coin-trade h5{
      color: #50535a;
      font-size: 14px;
      line-height: 50px;
    }
    .coin-trade p{
      color: #61688a;
      line-height: 25px;
      cursor: pointer;
      margin-bottom: 10px;
      &.coin-des{
        color: #b0b8db;
      }
    }
    .coin-trade  .table-title span{
      transition: all 0.2s;
    }
    .coin-trade-hidden p span{
      transform: rotate(180deg);
    }
    .coin-trade-hidden .coin-trade-detail{
      display: none;
    }
    .more-detail{
      padding-left: 140px;
    }
    .more-detail p span{
      color: #fff;
      padding-left: 10px;
    }
    .more-detail p a{
      color: #1fa8f6;
    }
    .more-detail p a:hover{
      text-decoration: underline;
    }
  }
</style>



